import axios from "./api/axios";

const WorkersService = {
    async getWorkersList (params = {}){
        try {
            return (
                await axios.get("/auth/users", {
                    params: {
                        ...params,
                    },
                })
            ).data;
        } catch (e) {
            console.log(e)
        }
    }
}
export default WorkersService
