<template>
  <TableView v-model:current-page="currentPage" v-model:page-size="pageSize" :all-pages-count="allPagesCount" :page-size-options="pageSizeOptions" @table-options-changed="getWorkersList">
    <template #filter-additional-content>
      <div class="w-full flex justify-between items-center">
        <div class="grid grid-cols-2 md:grid-cols-3 gap-4 2xl:w-[60%]" v-if="!isDirector">
          <!-- <div class="form_wrapper my-3">
            <input v-validate-cyrillic v-model="full_name" class="base_input" placeholder="F.I.O qidirish" type="text" />
          </div> -->
          <div v-if="position !== MANITORING_POSITION" class="form_wrapper my-3">
            <SelectBox id="center" v-model="center" class="w-full" :options="markazsList" show-by="name" track-by="id" placeholder="Markazni tanlang" />
          </div>
          <div v-else class="form_wrapper my-3">
            <SelectBox
              id="center"
              v-model="markaz_tuman"
              class="w-full"
              :options="addEmptyItemToSelectAllFilterOptions(districtList, 'name', 'id', 'Markaz - tuman tanlang')"
              show-by="name"
              track-by="id"
              placeholder="Markaz - tuman  tanlang"
            />
          </div>
          <div class="form_wrapper my-3">
            <SelectBox id="center" v-model="position" class="w-full" :options="positionsList" show-by="name" track-by="code" placeholder="Lavozimni tanlang" />
          </div>
        </div>
      </div>
    </template>
    <template #table-content>
      <div>
        <Loading v-if="loading" v-model:active="loading" :can-cancel="false" :is-full-page="true" color="#4785FE" loader="dots" />
        <TableWorkers v-else :table-body="workersList" :view-for="'minors'" />
      </div>
    </template>
  </TableView>
</template>

<script setup>
import {computed, defineEmits, defineProps, ref, toRefs, watch} from 'vue';
// import useRegion from '../../composables/selectbox-list/useRegion';
import Loading from 'vue-loading-overlay';
import 'vue-loading-overlay/dist/vue-loading.css';
import SelectBox from '../Common/SelectBox';
import TableView from './TableView';
import TableWorkers from './TableWorkers';
import useTableOptions from '../../composables/useTableOptions';
import {addEmptyItemToSelectAllFilterOptions} from '../../utils/selectboxOptionsHelper';
import useErrorAlert from '../../composables/useErrorAlert';
import useMarkazs from '@/composables/selectbox-list/useMarkazs';
import usePositions from '../../composables/selectbox-list/usePostions';
import useCentersToRegions from '../../composables/selectbox-list/useCentersToRegions';
import { useStore } from 'vuex';

const {alertThisError} = useErrorAlert();
const store = useStore()
const emit = defineEmits(['update:selectedWorker', 'see', 'delete', 'edit']);

const ROLE = 'Direktor';

const isDirector = computed(() => store.getters['getUserInfo'].role === ROLE);

const MANITORING_POSITION = 4;
const NAVBATCHI_POSITION = 3;

const props = defineProps({
  fetchWorkersListHandler: {
    type: Function,
  },
  selectedWorker: {
    type: Object,
  },
  updateWorkersList: {
    type: Boolean,
    default: true,
  },
});
const {updateWorkersList} = toRefs(props);

watch(
  () => updateWorkersList.value,
  (updateWorkersList) => {
    if (updateWorkersList) {
      getWorkersList();
    }
  }
);

const loading = ref(true);

// function emitWrapper(emitName,data){
//   emit(emitName,data)
// }
const workersList = ref([]);

const {markazsList} = useMarkazs();
const center = ref(null);
const {positionsList} = usePositions();
const position = ref(null);
const {regionList: districtList} = useCentersToRegions();
const markaz_tuman = ref(null);
watch(
  () => positionsList.value,
  () => {
    position.value = NAVBATCHI_POSITION;
  }
);

const {currentPage, pageSize, allPagesCount, pageSizeOptions} = useTableOptions([5, 7, 10]);

const getWorkersList = async (params) => {
  try {
    loading.value = true;
    const resp = await props.fetchWorkersListHandler({
      ...params,
    });
    workersList.value = resp?.results;
    console.log(workersList.value);
    allPagesCount.value = resp?.num_pages;
    currentPage.value = resp?.current;
    loading.value = false;
  } catch (e) {
    alertThisError(e);
  }
};

watch(
  [pageSize, currentPage, center, position, markaz_tuman],
  async ([newPageSize, newCurrentPage, center, position, markaz_tuman]) => {
    await getWorkersList({
      page: newCurrentPage,
      page_size: newPageSize,
      markaz: center,
      [position === MANITORING_POSITION ? 'markaz_tuman' : '']: markaz_tuman,
      position: position,
    });
  },
  {
    immediate: true,
  }
);
</script>

<style scoped></style>
